<style lang="less" scoped>
  .big_img{
    width: 100%;
    position: relative;
    text-align: center;
    min-height: 700px;
    &>img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      z-index: -1;
      height: 100%;
    }
    &>h1{
      color: #6dce67;
      position: absolute;
      bottom: 7%;
      left: 50%;
      transform: translateX(-50%);
      &>span{
        color: #333;
        font-size: 30px;
        font-weight: normal;
      }
      &>div{
        font-size: 40px;
        color: #6dce67;
      }
      &>.userSecurityInfoList{
        height: 100px;
        overflow-y: auto;
        &>p{
          font-size: 20px;
          height: 30px;
          line-height: 30px;
          color: #333;
        }
      }
    }
  }
  .middle_list{
    background-color: #fff;
    &>div{
      display: flex;
      height: 90px;
      line-height: 90px;
      &>span{
        flex: 1;
        display: inline-block;
        height: 90px;
        vertical-align: middle;
        font-size: 30px;
      }
      &>span:nth-child(1){
        text-indent: 1em;
      }
      &>span:nth-child(2){
        text-align: right;
        padding-right: 20px;
        box-sizing: border-box;
        &>img{
          vertical-align: middle;
          margin-left: 20px;
          width: 20px;
        }
      }
    }
  }
</style>
<template>
  <div>
    <headerNavBack :centerTitle="centerTitle"></headerNavBack>
    <div class="big_img">
      <img :src="userSecurity && userSecurity.icon" alt="">
      <h1>
        <div class="gradeScore">{{userSecurity && userSecurity.gradeScore}}</div>
        <span>账号安全等级{{userSecurity && userSecurity.statuValue}}</span>
        <section class="userSecurityInfoList">
          <p v-if="userSecurity && Array.isArray(userSecurity.msgvalue)" v-for="(item, index) in userSecurity.msgvalue" :key="index">{{item}}</p>
        </section>
      </h1>
    </div>
    <div class="middle_list">
      <div @click="__enterResetPasswd">
        <span>修改密码</span>
        <span><img :src="direction" alt="查看"></span>
      </div>
      <div @click="__enterChangePhone" v-if="0">
        <span>修改手机号码</span>
        <span><img :src="direction" alt="查看"></span>
      </div>
    </div>
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('userModule')
  
  export default {
    components: {
      headerNavBack
    },
    data() {
      return {
        centerTitle: '账号与安全',
        direction: require('../../assets/img/je3skwbl.png'),
        userSecurity: {
          msgvalue: ''
        }
      }
    },
    mounted() {
      this.GET_USER_SECURITY().then(data => {
        this.userSecurity = data.data.result
      })
    },
    watch: {

    },
    methods: {
      ...mapActions([
        'GET_USER_SECURITY'
      ]),
      __enterChangePhone(){
        this.$router.push({
          name: 'changePhone'
        });
      },
      back(){
        this.$router.back(-1)
      },
      __enterResetPasswd(){
        this.$router.push({
          name: 'resetPasswd'
        });
      }
    }
  }
</script>



